import React, { Component, useState, useEffect } from "react"; // import from react

import { Window, App, View, Button, Text, TextInput, StyleSheet, TouchableOpacity } from "proton-native"; // import the proton-native components

const TodoList = (props) => {

  const [data, setData] = useState(props);


  useEffect(() => {
    setData(props)
  },
  [props]
);
  return (
    <View 
      style={{
        width: "100%",
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-around",
        alignItems: "center",
        backgroundColor: "#EEEEEE",
        padding: 10,
        borderBottomWidth: 1,
        borderBottomColor: "#666666"
      }} 
    >
    <TouchableOpacity
      style={{
        borderWidth: 2,
        borderColor: "#7F39FB",
        backgroundColor: '#fff',
      }}
      onPress={() => data.checkTodo(data.todo.key)}
    >
      <Text
        style={{
          color: '#00ADEF',
          fontSize: 16,
        }}
      >
        {data.todo.isChecked ? "X" : "O"}
      </Text>
    </TouchableOpacity> 

    <Text>{data.todo.name}</Text>

    <TouchableOpacity
      style={{
        borderWidth: 2,
        borderColor: "#7F39FB",
        backgroundColor: '#fff',
      }}
      onPress={() => data.deleteTodo(data.todo.key)}
    >
      <Text
        style={{
          color: '#00ADEF',
          fontSize: 16,
        }}
      >
        {`delete:${data.todo.key}`}
      </Text>
    </TouchableOpacity> 
    </View>
  );
}

export default TodoList;